body {
  font-family: var(--poppins-family);
  color: var(--main-color);
  margin: 0;
}

ul,
ol,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

button {
  color: inherit;
  margin: 0;
  padding: 0;
  outline: none;
}

img {
  display: block;
  max-width: 100%;
}

a {
  outline: none;
  text-decoration: none;
}

input {
  outline: none;
}

.container {
  max-width: 480px;
  margin: 0 auto;

  @media screen and (min-width: $tablet) {
    max-width: 768px;
  }

  @media screen and (min-width: $desctop) {
    max-width: 1280px;
  }
}




